
@charset "UTF-8";
body, div, span, header, footer, nav, section, aside, article, ul, dl, dt, dd, li, a, p, h1, h2, h3, h4, h5, h6, i, b, textarea, button, input, select, figure, figcaption {
  padding: 0;
  margin: 0;
  list-style: none;
  font-style: normal;
  text-decoration: none;
  border: none;
  font-weight: normal;
  font-family: '-apple-system-font', "Helvetica Neue", sans-serif;
  -webkit-tap-highlight-color: transparent;
  -webkit-font-smoothing: antialiased;
}
body:hover, div:hover, span:hover, header:hover, footer:hover, nav:hover, section:hover, aside:hover, article:hover, ul:hover, dl:hover, dt:hover, dd:hover, li:hover, a:hover, p:hover, h1:hover, h2:hover, h3:hover, h4:hover, h5:hover, h6:hover, i:hover, b:hover, textarea:hover, button:hover, input:hover, select:hover, figure:hover, figcaption:hover {
    outline: none;
}
body {
  height: 100%;
  width: 100%;
  background-color: #F3F3F3;
  overflow-x: hidden;
}

/*定义滚动条高宽及背景 高宽分别对应横竖滚动条的尺寸*/
::-webkit-scrollbar {
  width: 0px;
  height: 0px;
  background-color: #F3F3F3;
}

/*定义滚动条轨道 内阴影+圆角*/
::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 1px transparent;
  border-radius: 10px;
  background-color: #F3F3F3;
}

/*定义滑块 内阴影+圆角*/
::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #696969;
}
input[type="button"], input[type="submit"], input[type="search"], input[type="reset"] {
  -webkit-appearance: none;
}
textarea {
  -webkit-appearance: none;
}
html, body {
  height: 100%;
  width: 100%;
}
.clear:after {
  content: '';
  display: block;
  clear: both;
}
.clear {
  zoom: 1;
}
.back_img {
  background-repeat: no-repeat;
  background-size: 100% 100%;
}
.margin {
  margin: 0 auto;
}
.left {
  float: left;
}
.right {
  float: right;
}
.hide {
  display: none;
}
.show {
  display: block;
}
.ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.paddingTop {
  padding-top: 1.95rem;
}
@-webkit-keyframes backOpacity {
0% {
    opacity: 1;
}
25% {
    opacity: .5;
}
50% {
    opacity: 1;
}
75% {
    opacity: .5;
}
100% {
    opacity: 1;
}
}
@keyframes backOpacity {
0% {
    opacity: 1;
}
25% {
    opacity: .5;
}
50% {
    opacity: 1;
}
75% {
    opacity: .5;
}
100% {
    opacity: 1;
}
}
.animation_opactiy {
  -webkit-animation: backOpacity 2s ease-in-out infinite;
          animation: backOpacity 2s ease-in-out infinite;
}
.small-p {
  font-size: 12px !important;
  color: #cbcbcb !important;
}
.mmall-p {
  font-size: 12px !important;
  color: #cecece !important;
}
.lmall-p {
  font-size: 13px !important;
  color: #696969 !important;
}
.article-p {
  color: #696969 !important;
  text-align: left;
  text-indent: 20px;
  padding: 5px 0px;
  font-size: 14px;
}
.title-p {
  color: #454545 !important;
  font-size: 15px !important;
}
.main-p {
  color: #696969 !important;
  font-size: 15px !important;
}
.minor-p {
  color: #9c9c9c !important;
  font-size: 12px !important;
}
.btn-color {
  margin-top: 10px;
  background-color: #fec470 !important;
  color: #fff !important;
  font-size: 18px;
}
.vux-swipeout-button {
  font-size: 16px !important;
  background-color: #ff0000 !important;
}
.weui-panel__hd {
  font-size: 15px !important;
  color: #454545 !important;
}
.weui-cells {
  font-size: 15px !important;
  color: #696969 !important;
}
select {
  color: #696969 !important;
}
.weui-tabbar__item.weui-bar__item_on .weui-tabbar__label {
  color: #fdc470 !important;
}
#app {
  height: 100%;
}
#app .vux-header {
    position: relative;
    padding: 3px 0;
    box-sizing: border-box;
    background-color: #fdc470;
}
#app .vux-header a,
    #app .vux-header h1 {
      color: #fff;
      font-size: 18px;
}
#app .vux-header .vux-header-left .left-arrow:before {
      border: 1px solid #fff;
      border-width: 2px 0 0 2px;
      top: 9px;
}
#app .vux-no-group-title {
    margin-top: 10px;
}



